import { POST } from '../../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, error } from "../../../service/MessageBox"
import moment from "moment";

export default {
  name: "input-client",
  components: { HalfCircleSpinner },
  props: ["form", "onClose", "loadData", "textTitle", "disableView", "disableEdit"],
  data() {
    return {
      isLoading: true
    };
  },
  beforeMount: function () { },
  mounted() { },
  methods: {
    onSave: function () {
      try {
        this.isLoading = true
        let dataReq = {
          Id: this.form.Id,
          StartDate: moment(this.form.StartDate).format("YYYY-MM-DD HH:mm:ss"),
          EndDate: moment(this.form.EndDate).format("YYYY-MM-DD HH:mm:ss"),
          EmployeeID: this.form.EmployeeID,
          TaskDesc: this.form.TaskDesc,
          Assignee: this.form.Assignee,
        }
        if (dataReq.Id === null) {
          POST("monitoringschedule", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Disimpan")
            this.onClose()
          })
        } else {
          POST("monitoringschedule", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Di Update")
            this.onClose()
          })
        }
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
  }
};
