import formScheduleMonitoring from "./formScheduleMonitoring";
import { POST, GET, PUT, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formScheduleMonitoring, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataScheduleMonitoring: [],
      form: {
        Id: null,
        StartDate: null,
        EndDate: null,
        EmployeeID: null,
        TaskDesc: null,
        Assignee: null,
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Start Date",
        value: "StartDate",
        align: "center"
      }, {
        text: "End Date",
        value: "EndDate",
        align: "center"
      }, {
        text: "Employee ID",
        value: "EmployeeID",
        align: "center"
      }, {
        text: "Task Desc",
        value: "TaskDesc",
        align: "center"
      }, {
        text: "Assignee",
        value: "Assignee",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Schedule Monitoring"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`monitoringschedule/monitoringschedule_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataScheduleMonitoring = res.Data.map(x => {
            return ({
              Id: x.Id,
              StartDate: x.StartDate,
              EndDate: x.EndDate,
              EmployeeID: x.EmployeeID,
              TaskDesc: x.TaskDesc,
              Assignee: x.Assignee,
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`monitoringschedule/monitoringschedule_cr/${page}/${rowsPerPage}`, { Id: id }).then((res) => {
          this.form = res.Data[0]
          this.disableView = false;
          this.disableEdit = true;
          this.textTitle = "Ubah Schedule Monitoring"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`monitoringschedule/${id}`).then((res) => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        const { page, rowsPerPage } = this.pagination;
        POST(`monitoringschedule/monitoringschedule_cr/${page}/${rowsPerPage}`, { Id: id }).then((res) => {
          this.form = res.Data[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Schedule Monitoring"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        StartDate: null,
        EndDate: null,
        EmployeeID: null,
        TaskDesc: null,
        Assignee: null,
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`monitoringschedule/monitoringschedule_cr/${page}/${rowsPerPage}`).then((res) => {
            this.dataScheduleMonitoring = res.Data.map(x => {
              return {
                Id: x.Id,
                StartDate: x.StartDate,
                EndDate: x.EndDate,
                EmployeeID: x.EmployeeID,
                TaskDesc: x.TaskDesc,
                Assignee: x.Assignee,
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
